<template>
  <Container
    box-shadow
    background-color="white"
    radius="16px"
    class="admin-registration"
  >
    <div class="admin-registration__header">
      <div class="admin-registration__header-logo">
        <Icon
          width="147px"
          height="24px"
          name="logo-icon"
        />
      </div>
      <ProgressBar
        :step="currentStep"
        :steps-count="stepsCount"
      />
    </div>

    <keep-alive>
      <component
        :is="'Step' + currentStep"
        @submit="onNext"
      />
    </keep-alive>

    <Lang dropdownSide="bottom">
      <template v-slot:default="{ lang }">
        <div class="d-flex align-items-center">
          <Icon :name="lang.label"/>
          <Text size="14px" line-height="16px" ml="8px" mr="5px">{{ lang.text }}</Text>
          <Icon name="arrow-down"/>
        </div>
      </template>
    </Lang>
  </Container>
  <div class="admin-registration__restricted">
    <Icon name="access" />
    <Text opacity="0.4" mt="2px" ml="9px">{{ tp('restrictedAccess') }}</Text>
  </div>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useRoute, useRouter } from 'vue-router'
import { defineAsyncComponent, ref } from 'vue'
import Text from '@/shared/components/UIKit/Text/Text'
import Lang from '@/shared/components/UIKit/Lang'
import Icon from '@/shared/components/Icon/Icon'
import Button from '@/shared/components/UIKit/Button'
import Container from '@/shared/components/Container'
import ProgressBar from '@/admin/components/ProgressBar'

export default {
  name: 'Registration',
  components: {
    Text,
    Lang,
    Icon,
    Button,
    Container,
    ProgressBar,
    Step1: defineAsyncComponent(() => import('@/admin/views/Registration/Steps/Step1')),
    Step2: defineAsyncComponent(() => import('@/admin/views/Registration/Steps/Step2')),
    Step3: defineAsyncComponent(() => import('@/admin/views/Registration/Steps/Step3'))
  },
  setup () {
    const route = useRoute()
    const router = useRouter()
    const { tp } = useI18n('adminRegistration')
    const stepsCount = ref(3)
    const currentStep = ref(Number(route.query.step) || 1)

    const onNext = () => {
      const newStep = currentStep.value + 1
      currentStep.value = newStep > stepsCount.value ? stepsCount.value : newStep
      router.replace({
        ...route,
        query: {
          step: newStep
        }
      })
    }

    return {
      tp,
      stepsCount,
      currentStep,
      onNext
    }
  }
}
</script>

